h3{
  padding: 50px;
  display: flex;
  flex-direction: row;
  justify-content: center;
  text-align: center;
}
h3:before, h3:after {
  background-color: black;
  content: '\a0';
  flex-grow: 1;
  height: 1px;

  position: relative;
  top: 0.5em;
}
h3:before {
  margin-right:10px;
}
h3:after {
  margin-left:10px;
}
