    text-decoration: none;
    font-size: 18px;
    line-height: 60px;
    transition: 0.2s;
}

.button:hover{
    -webkit-filter: brightness(90%);
            filter: brightness(90%);
}

/* The navigation menu */
.nave-bar {
    overflow: hidden;
    background-color: #333;
}
  
  /* Navigation links */
  .nave-bar a {
    float: left;
    font-size: 16px;
    color: white;
    text-align: center;
    padding: 14px 16px;
    text-decoration: none;
  }
  
  /* The subnavigation menu */
  .sub-nave {
    float: left;
    overflow: hidden;
  }
  
  /* Subnav button */
  .sub-nave .subnavebnt {
    font-size: 16px;
    border: none;
    outline: none;
    color: white;
    padding: 14px 16px;
    background-color: inherit;
    font-family: inherit;
    margin: 0;
  }
  
  /* Add a red background color to navigation links on hover */
  .nave-bar a:hover, .sub-nave:hover .subnavebnt {
    background-color: #e02041;
  }
  
  /* Style the subnav content - positioned absolute */
  .sub-naver-content {
    display: none;
    position: absolute;
    left: 0;
    background-color: #e02041;
    width: 100%;
    z-index: 1;
  }
  
  /* Style the subnav links */
  .sub-naver-content a {
    float: left;
    color: white;
    text-decoration: none;
  }
  
  /* Add a grey background color on hover */
  .sub-naver-content a:hover {
    background-color: #eee;
    color: black;
  }
  
  /* When you move the mouse over the subnav container, open the subnav content */
  .sub-nave:hover .sub-naver-content {
    display: block;
  }

