}
.card:hover{
  background: linear-gradient( #A19E9E 0%,#E1DDDD 100%);
}
.card h1{
  margin-top: 40px;
  font-weight: 700 18px;
  
}
.card h2{
  font-weight: 400 10px;
  font-size: 15px;
}
.card h1,h2{
