}

.shopping-list{
  margin-top: 20px;
  display:flex;
  align-items:center;
  justify-content:space-between;
  margin: 0 auto;
}

.shop-button{
  margin: 0 auto;
  margin-top: 20px;
  padding: 20px;
  background:#e02041;
  color: black;
  width:30%;
  border:1px solid #A19E9E;
  box-shadow:0 2px 3px 0px rgba(0,0,0,0.25);
  border-radius:3px;
  text-decoration: none;
  justify-content: space-between;
  text-align: center;
  font-weight: 700;
}

.shopping-list:hover .shop-button{
  -webkit-filter:blur(1px);
          filter:blur(1px);
  opacity:.9;
  transform: scale(.98);
  box-shadow:none;
}

.shopping-list:hover .shop-button:hover{
  transform:scale(1);
  -webkit-filter:blur(0px);
          filter:blur(0px);
  opacity:1;
  box-shadow:0 8px 20px 0px rgba(0,0,0,0.125);
}

.back-link {
  display: flex;
  align-items: center;
  margin-bottom: 8px;
  margin-left: 40px;
  color: #E02041;
  font-size: 15px;
  text-decoration: none;
  font-weight: bold;
  transition: opacity 0.2s;
}
.back-link  svg{
  margin-right: 8px;
}
.back-link:hover{
